.header-color{ 
  background: #159957;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{ 
  /* background: #159957;  /* fallback for old browsers */
  /* background: -webkit-linear-gradient(to right, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
  /* background: linear-gradient(to right, #155799, #159957); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
}

.projects-grid{
  display: flex;
}

.prof-photo{
  height: 250px;
  /* padding-top: 5em; */
}

.spacer {
  padding-top: 2em;
}

.links{
  color: white;
  text-decoration: none;
}

.side-links{
  color: black;
  text-decoration: none;
}

.live-links{
  color: #159957;
  text-decoration: none;
  font-weight: bold;
}

.git-links{
  color: #155799;
  text-decoration: none;
  font-weight: bold;
}

.banner-text{
  background-color: rgba(0, 0, 0, 0);
  text-align: center;
  justify-content: center;
  width: 90vw;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 4em;
  font-weight: bold;
  font-family: "Merienda", cursive, sans-serif;
  color: rgba(255, 255, 255, 0.705);
  /* opacity: 0.7; */
}

.banner-text p{
  font-size: 1.1em;
  font-weight: bold;
  font-family: "Hind Siliguri", cursive, sans-serif;
  color: rgba(255, 255, 255, 0.95);
  overflow-wrap: break-word;
}

.footer {
  height: 1px;
  min-height: 1px;
  color: black;
  opacity: 0.8;
  text-align: center;
  padding-top: 10px;
}

.mdl-mini-footer {
  padding-top: 10px;
}

.contact-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 300px;
  height: auto;
  background: rgba(255, 255, 255, 0.493);
  /* opacity: 0.5; */
  color: black;
  /* border: 2px solid red; */
  margin-bottom: 20px;
  border-radius: 20px;
}

.contact-grid h2 {
  font-family: 'Proza Libre', sans-serif;
}

.contact-grid img {
  border-radius: 15px;
  margin-bottom: 10px;
  margin-top: 20px;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 60%; 
  margin: auto;
}

.contact-list {
  display: flex;
  justify-content: center;
  text-align: center;
}

.contact-list i {
  font-size: 50px;
  padding-right: 1rem;
}

.linkedin {
  color: #0077B5;
  text-decoration: none;
}

.github {
  color: #6e5494;
  text-decoration: none;
}

.resume-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  /* justify-content: center; */
  padding-top: 2em;
}

.resume-grid {
  text-align: left;
  /* justify-content: center; */
  width: 60%;
  height: auto;
  background: rgba(255, 255, 255, 0.65);
  /* opacity: 0.5; */
  color: black;
  /* border: 2px solid red; */
  margin-bottom: 20px;
  border-radius: 20px;
  display: flex;
}

.aboutme-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 1em;
}

.aboutme-grid {
  text-align: center;
  justify-content: center;
  width: 60%;
  height: auto;
  background: rgba(255, 255, 255, 0.65);
  /* opacity: 0.5; */
  color: black;
  /* border: 2px solid red; */
  margin-bottom: 20px;
  border-radius: 20px;
}

.about-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.aboutme-grid img {
  border-radius: 15px;
  margin-bottom: 10px;
  margin-top: 10px;
}

.aboutmephoto{
  height: auto;
  width: 100%;
  align-self: right;
}

.about-quote {
  font-family: 'Merienda', sans-serif;
  font-weight: bold;
  font-size: 1.3em;
}

.about-text {
  font-family: 'PT Sans', sans-serif;
  font-size: 1.1em;
}

.txt {
  font-family: 'PT Sans', sans-serif;
  color: #159957;
  text-decoration: none;
  font-weight: bold;
}

.resume-section hr {
  border-top: 5px solid rgba(0, 0, 0, 0.315);
  width: 100%; 
}

.resume-grid h3{
  font-size: 20px;
  margin-top: 0px;
  margin-bottom: 0px;
  font-family: 'Merienda', sans-serif;
}

.resume-grid h4{
  font-size: 17px;
  margin-top: 10px;
  margin-bottom: 0px;
  font-family: 'PT Sans', sans-serif;
}

.schoolimg {
  height: 80px;
  /* width: 100%; */
  float: right;
}

.resume-subtitle {
  margin-bottom: 5px;
  font-family: 'PT Sans', sans-serif;
}

#uwimg {
  margin-right: 40px;
}

#vtimg {
  height: 60px;
  margin: 10px 0px;
  float: right;
}

.skills {
  font-size: 17px;
  margin-top: 10px;
  margin-bottom: 0px;
}

